"use client";
import React from "react";
import { Button, Drawer } from "@mui/material";
import { DarkModeOutlined, LightModeOutlined, ShoppingCartOutlined } from "@mui/icons-material";
import CartToggleDrawer from "@/public/utils/helpers/cart/cart-toggleDrawer";
const TopBar = (prop: any) => {
  const [cart, setCart] = React.useState(false);
  const [lightMode, setLightMode] = React.useState(true);
  const toggleDrawer =
    (open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }

      setCart(open);
    };
    const eventLightMode= () =>{
        setLightMode(!lightMode);
  
    }
  return (
    <div className="top-bar max-w-full text-right">
      <Button onClick={toggleDrawer(true)}>
        <ShoppingCartOutlined />
      </Button>
      <Drawer anchor={"right"} open={cart} onClose={toggleDrawer(false)}>
        {CartToggleDrawer(toggleDrawer)}
      </Drawer>
      <Button onClick={eventLightMode}>
      {  lightMode ? <LightModeOutlined /> : <DarkModeOutlined/>} 
      </Button>
      
    </div>
  );
};
export default TopBar;
